import { createRouter, createWebHashHistory } from 'vue-router'; // 引入 createWebHashHistory
import AboutPage from '../views/AboutPage.vue';
import LoginPage from '../views/LoginPage.vue'; 
import MePage from '../views/MePage.vue'; 
import RoomsPage from '../views/RoomsPage.vue'; 
import FriendsPage from '../views/FriendsPage.vue'; 
import ChatPage from '../views/ChatPage.vue'; 
import AddFriendPage from '../views/AddFriendPage.vue'; 
import FindPage from '../views/FindPage.vue'; 

const routes = [
  {
    path: '/',
    name: 'LoginPage',
    component: LoginPage,
  },
  {
    path: '/rooms',
    name: 'RoomsPage',
    component: RoomsPage,
  },
  {
    path: '/friends',
    name: 'FriendsPage',
    component: FriendsPage,
  },

  {
    path: '/friends/add',
    name: 'AddFriendPage',
    component: AddFriendPage,
  },
  {
    path: '/chat',
    name: 'ChatPage',
    component: ChatPage,
  },

////////////////find////////////////////
  {
    path: '/find',
    name: 'FindPage',
    component: FindPage,
  },

////////////////me/////////////////  
{
  path: '/me',
  name: 'MePage',
  component: MePage,
},
{
  path: '/about',
  name: 'AboutPage',
  component: AboutPage,
},



];

const router = createRouter({
  history: createWebHashHistory(), // 使用 createWebHashHistory
  routes
});

export default router;
